* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  border-radius: 7px;
  margin-top: 16px;
  margin-bottom: 16px;
  margin-left: 16px;
  padding: 8px;
}

.country-list {
  max-width: fit-content;
  padding: 10px;
  list-style: none;
}

.country-item {
  opacity: 0;
  align-items: center;
  animation: fadeinout .5s linear forwards;
  display: flex;
}

.listed-country {
  max-width: 400px;
  vertical-align: middle;
  background: rgba(33, 33, 33, .267);
  border-radius: 3px;
  align-items: center;
  padding: 10px;
  font-weight: 700;
}

.flag {
  width: 60px;
  height: 40px;
  border: 2px solid rgba(33, 33, 33, .176);
  border-radius: 3px;
  margin: 10px;
  box-shadow: 5px 5px 10px -6px #42445a;
}

.container {
  max-width: 400px;
  background-color: bisque;
  border: 3px dotted rgba(0, 0, 0, .2);
  border-radius: 7px;
  flex-direction: column;
  display: flex;
}

.country-info {
  opacity: 0;
  margin-left: 16px;
  font-size: large;
  animation: fadeinoutcountry .5s linear forwards;
}

.country-name {
  font-size: 32px;
  font-weight: 800;
}

.country-info-flag {
  max-width: 200px;
  border: 2px solid rgba(33, 33, 33, .176);
  border-radius: 3px;
  box-shadow: 5px 5px 10px -6px #42445a;
}

@-webkit-keyframes fadeinout {
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeinoutcountry {
  100% {
    opacity: 1;
  }
}

